<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <div style='font-size: 14pt;font-weight:800'>
        
      </div>
      <el-row :gutter="20" style='margin-top: 10px;margin-left: 20px'>
        <!---->
        <el-col :span="4">
          <el-row>
            <el-col :span="8">
              <div style='font-weight: bold;margin-top: 10px;font-size: 10pt'></div>
            </el-col>
            <el-col :span="16">
              <el-select v-model="fundType" placeholder="">
                <el-option
                  v-for="(item, index) in fundOptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-col>
        <!---->
        <el-col :span="9">
          <el-row>
            <el-col :span="4">
              <div style='font-weight: bold;margin-top: 10px;font-size: 10pt'></div>
            </el-col>
            <el-col :span="20">
              <el-date-picker
                v-model="dateRange"
                type="date"
                placeholder="">
              </el-date-picker>
            </el-col>
          </el-row>
        </el-col>
        <!---->
        <el-col :span="6">
          <el-button type="primary" @click="getFundList"></el-button>
          <el-button type="danger" @click="clearSearch"></el-button>
        </el-col>
      </el-row>

      <div style='font-size: 12pt; margin-top:25px' >
        <h3></h3>
      </div>
      <el-tag :type="tagType" v-if="tagVisable" @click="showConfirmDialog" ref="tagRef"></el-tag>
    </el-card>

    <!--      -->
    <el-dialog title="" :visible.sync="confirmDialogVisible" width="50%">
     <h4 align='center' >{{fundDetailInfo.title}}</h4>
      <p style='text-indent: 2em;color: #409EFF'>{{fundDetailInfo.description}}</p>
      <span style="color: red"><span style="font-weight: bold"></span>{{fundDetailInfo.capitalAmount}}</span><br>
      <span style="display: flex; justify-content: flex-end;  font-weight: bold">{{fundDetailInfo.capitalTime}}</span>
      <span slot="footer" class="dialog-footer">
          <el-button @click="confirmDialogVisible = false"> </el-button>
          <el-button type="primary" @click="confirmFunds"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>

export default {
  data() {
    return {
      //
      gjjzh: '',
      //
      confirmDialogVisible: false,
      // 
      fundType: '',
      // 
      fundOptions: [
        {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }
      ],
      // 
      dateRange: '',
      // 
      tagVisable: false,
      // 
      fundDetailInfo: {},
      // tag
      tagType: 'primary'
    }
  },
  created() {
    this.gjjzh = window.sessionStorage.getItem('gjjzh')
  },
  // 
  methods: {
    // 
    async getFundList() {
      if (this.fundType === '') return this.$message.error('')
      if (this.dateRange === '') return this.$message.error('')
      const dateFormat = this.dateRange.getFullYear() + '-' + (this.dateRange.getMonth() + 1 + '').padStart(2,'0') + '-' +  (this.dateRange.getDate() + '').padStart(2,'0')
      const { data: res } = await this.$http.get('/qtzj/findQtzjYuE', { params: {
          gjjzh: this.gjjzh ,
          localDate: dateFormat,
          zjly: this.fundType
        }})
      if( res.code !== 200) return this.$message.error('')
      this.tagVisable =true
      this.fundDetailInfo = res.data
      console.log(res)
    },
    // 
    showConfirmDialog() {
      this.confirmDialogVisible = true
    },
    confirmFunds: function() {
      this.tagType = 'success'
      this.$message.success('')
      this.confirmDialogVisible = false;
    },
    // 
    clearSearch() {
      this.dateRange = ''
      this.fundType = ''
      this.tagVisable = false
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .el-dialog__body {
  padding: 0 20px!important;
}
.el-tag {
  height: 60px;
  width: 120px;
  line-height: 60px;
  text-align: center;
  font-size: 15px;
  font-weight: bold;
}
</style>
